* {
    font-family: "Bricolage Grotesque", sans-serif;
    font-weight: 300;
    color : #02060cba;
    background-color: #ffffff;
}

.header{
    display: flex;
    justify-content: space-between;
    margin: 10px;
    border: 1px solid black;
}

.nav-items > ul{
    display: flex;
    list-style-type: none;
}

.nav-items > ul > li{
    padding: 10px;
}

.logo{
    width: 100px;
}

.card{
    width: 200px;
    border: 1px solid black;
    padding: 10px;
    margin: 10px;
}

.card > img {
    width: 100%;
}

.restaurant-list{
    display: flex;
    flex-wrap: wrap;
}

.shimmer-card{
    width: 220px;
    height: 480px;
    background: lightgray;
    margin: 10px;
}

.menu {
    display: flex;
}
